import React, { useState, useEffect } from "react";
import axios from "axios";

function Currency() {
  let [curr, setCurr] = useState([]);
  useEffect(() => {
    async function fetchData() {
      const result = await axios.get("/api/getCurrency");
      //`https://cors-anywhere.herokuapp.com/https://poe.ninja/api/data/CurrencyOverview?league=Scourge&type=Currency`
      //`api/data/CurrencyOverview?league=Scourge&type=Currency`
      //console.log(result.data.lines[0]);
      console.log(result);
      setCurr(result.data);
    }
    fetchData();
  }, []);
  let listItems = curr.map((cur, id) => (
    <tr key={id}>
      <td>
        {cur.currencyTypeName} --- {cur.chaosEquivalent}
      </td>
    </tr>
  ));
  return (
    <table>
      <tbody>{listItems}</tbody>
    </table>
  );
}

export default Currency;
